.App {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: 15px;
  flex-direction: column;
  width: 50%;
  border: 3px solid rgb(130, 130, 180);
  border-radius: 10px;
  background-color: #9bc8df;

  &__select {
    margin: 15px;
  }

  &__error {
    color: #ff0000;
  }
}
